@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.project-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  grid-column-gap: 1.8em;
  -webkit-column-gap: 1.8em;
          column-gap: 1.8em;
  padding-bottom: 3em;
}

h3 {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: 1.5em;
  color: grey;
}

.project-description {
  font-family: 'Roboto', sans-serif;
  color: grey;
  padding-top: 3em;
  font-size:0.8em;
  text-align: center;
}

.text-right {
  grid-column: span 2;
}

.text-left {
  grid-column: 2 / span 2;
}

.badge-container-left {
  grid-column: 2;
  text-align: -webkit-right;
  padding-bottom: 1.5em;
}

.badge-container-right {
  grid-column: 4;
  text-align: -webkit-left;
  padding-bottom: 1.5em;
}

.badge {
  height: 12em;
  width: 12em;
  overflow: hidden;
  border-radius: 50%;
  box-shadow: 2px 5px 8px lightgrey;
}

.badge:hover {
  box-shadow: 2px 5px 8px grey;
}

.badge img {
  height: 100%;
}

.ascii-badge {
  height: 12em;
  width: 12em;
  overflow: hidden;
  border-radius: 50%;
  box-shadow: 2px 5px 8px lightgrey;
}

.ascii-badge img {
  margin-left: -525px;
  margin-top: -400px;
}

.ascii-badge:hover {
  box-shadow: 2px 5px 8px grey;
}

h1 {
  font-family: 'Commissioner', sans-serif;
  font-weight: 200;
  font-size: 3em;
  word-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
  min-width: 100vw;
  display: inline-block;
}

h2 {
  font-family: 'Commissioner', sans-serif;
  font-weight: 200;
  font-size: 2em;
  word-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
  padding-top: 2em;
}

ul {
  display: inline-block;
  list-style-type: none;
  margin: 0;
  padding-bottom: 4.5em;
  padding-left: 0em;
  text-align: center;
  width: 100vw;
  max-width: 100vw;
}

li {
  font-family: 'Roboto', sans-serif;
  font-size: min(2vw, 0.8em);
  text-transform: uppercase;
  display: inline;
}

li:last-child:after {
  content: '';
  padding: 0em;
}

li:after {
  content: "\00B7";
  padding: 1.2em;
}

a:link, a:visited {
  color: black;
  text-decoration: none;
}

a:hover {
  color: grey;
  text-decoration: none;
}

a.active {
  color: chocolate;
  text-decoration: none;
}

